<script>
import { useRouter } from "vue-router"
// import { ExtCall } from "@/utils/extCall"
import { useGlobalStore } from "@/store/global"

export default {
  props: {
    title: String,
    right: String,
    path: String,
    backToPath: String,
  },
  setup(props) {
    const globalStore = useGlobalStore()
    const globalGoto = globalStore.goto

    const router = useRouter()
    let iconCode = ""
    let text = ""
    const state = props.right ? props.right : ""
    if (state == "add") {
      iconCode = "fa fa-plus"
      text = " 新增"
    }
    if (state == "map") {
      iconCode = "fas fa-map-marker-alt"
      text = " 地圖"
    }
    if (state == "text") {
      iconCode = "far fa-list-ul"
      text = " 列表"
    }
    if (state == "search") {
      iconCode = "far fa-search"
      text = " 搜尋"
    }

    const goto = () => {
      if (props.path == "search") {
        console.log("search")
      } else {
        router.push({ path: props.path })
      }
    }
    const backTo = () => {
      if (props.backToPath == "-1") {
        try {
          globalGoto("back")
        } catch (error) {
          console.log(error)
          router.back()
        }
      } else if (props.backToPath) {
        router.push({ path: props.backToPath })
      } else {
        router.push({ path: "/home" })
      }
    }

    return { iconCode, text, backTo, goto }
  },
}
</script>

<template>
  <header class="c-header">
    <nav class="navbar ui-navbar">
      <ul class="navbar-nav">
        <li class="nav-item" @click="backTo"> 
          <a class="nav-link"
            ><i class="fa fa-chevron-left"></i
          ></a>
        </li>
      </ul>
      <h1 class="navbar-brand">{{ title }}</h1>
      <ul class="navbar-nav">
        <li class="nav-item">
          <a class="nav-link"
            ><i :class="iconCode" @click="goto"
              ><span>{{ text }}</span></i
            ></a
          >
        </li>
      </ul>
    </nav>
  </header>
</template>

<style lang="scss" scoped>
// @import "../../../assets/layout.scss";
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";

// header
.c-header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
}

.ui-navbar {
  padding-top: 0;
  padding-bottom: 0;
  height: $header-height;
  background-color: $primary-color;

  .nav-link {
    padding-left: 0.875rem;
    padding-right: 0.875rem;
    color: white;
    font-size: 0.8125rem;
    line-height: 1;
    white-space: nowrap;

    i {
      font-size: 1.125rem;
      color: white;
    }

    span {
      font-size: 0.8125rem;
      padding-left: 0.25rem;
    }

    .fa-chevron-left {
      font-size: 1.25rem;
    }
  }
}

.navbar-brand {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-right: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  color: white;
  font-size: 1.25rem;
  white-space: nowrap;
  @include translate(-50%, -50%);

  img {
    height: 2.1875rem;
  }
}
</style>
