<script>
import { ref, onMounted, onUnmounted } from 'vue'
import Header from "@/components/global/header/HeaderArea.vue"
import Footer from "@/components/global/footer/FooterArea.vue"
import { useRouter } from "vue-router"
import NoData from "@/components/global/NoData.vue"
import { apiGetNotice, apiGetPinkNotice } from "@/api/api"
import { isBetweenBottom } from "@/utils/helper"


export default {
    setup() {
        const router = useRouter()
        const noticeData = ref({})
        const page = ref(1)
        let totalData = 0
        let getApiTimer = null
        onMounted(async () => {
            if (process.env.VUE_APP_MODE == 'purple') {
                noticeData.value = await apiGetNotice({ page: page.value, limit: 10 })
            } else {
                noticeData.value = await apiGetPinkNotice({ page: page.value, limit: 10 })
            }
            totalData = noticeData.value.data.total
            noticeData.value = noticeData.value.data.notify
            // console.log(noticeData.value)

            //每0.5S監測是否在底部
            getApiTimer = setInterval(() => {
                if (isBetweenBottom()) {
                    // 超過上限不做GET
                    if (noticeData.value.length < totalData) {
                        handleScroll();
                    }
                }
            }, 500)
        })

        //滾動載入資料
        const handleScroll = async () => {
            page.value += 1;
            let newData = ref()
            // console.log(page.value)
            if (process.env.VUE_APP_MODE == 'purple') {
                newData.value = await apiGetNotice({ page: page.value, limit: 10 })
            } else {
                newData.value = await apiGetPinkNotice({ page: page.value, limit: 10 })
            }
            noticeData.value = noticeData.value.concat(newData.value.data.notify);
        };
        //取消監控
        onUnmounted(() => clearInterval(getApiTimer))

        const goto = (val) => {
            router.push({ path: val })
        }
        const linkHandle = (routerType, routerId) => {
            if (routerType == 1) {
                return `/schedule/info/${routerId}`
            } else if (routerType == 2) {
                return `/order/info/${routerId}`
            } else if (routerType == 3) {
                return `/notice`
            } else {
                return `/notice`
            }
        }

        return { noticeData, goto, linkHandle }
    },
    components: {
        Header, Footer, NoData
    },
}
</script>

<template>
    <Header title="通知提醒" />
    <div class="notice">
        <section class="c-main">
            <div class="notice-container">
                <NoData v-if="noticeData.length == 0" />
                <div class="card" v-for="(item, index) in noticeData" :key=index>
                    <div class="card-header">
                        <a href="javascript:void(0);" class="card-link">
                            <i class="far fa-bell"></i>
                        </a>
                    </div>
                    <div class="card-body">
                        <a @click="goto(linkHandle(item.routerType, item.routerId))" class="card-link">
                            <div class="card-title">{{ item.title }}</div>
                            <div class="card-text" style="white-space: pre-wrap;">{{ item.message }}</div>
                            <div class="card-date">{{ item.dateTime }}</div>
                        </a>
                    </div>
                </div>
            </div>
        </section>
    </div>
    <Footer />
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.notice {
    margin-top: $header-height;
    margin-bottom: calc($footer-height + 15px);
}
</style>